import React, { Component } from 'react';

class About extends Component {
    render() {
        return(
            <div className="condiv">
                <h1 className="subtopic">About Me</h1>
                <h4>Hello there!</h4>
                <h1>My name is Emily Valenti</h1>
                <h3>I'm a Web Design|Developer and Mobile Application Developer in training </h3>
                <p>I have always been interested in computer technologies. My bother has dabbled in Web design and
                    development and seeing him create different websites was what really inspired me! I am wanting to 
                    be a Mobile Application Developer, but the more skill sets that I can aquire the more versitile
                    and valuable I can be as a developer! I have really enjoyed exploring some of the different programming
                    languages (I know that I haven't even delved the depths!) and look forward to learning more. 
                </p>
            </div>
        )
    }
}

export default About


